import React from "react";
import styled from "styled-components";
import { InnerLayout } from "../styles/Layouts";
import Title from "../Components/Title";
import ProgressBar from "./ProgressBar";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fab } from "@fortawesome/free-brands-svg-icons";
import { faCamera } from "@fortawesome/free-solid-svg-icons";

function Skills() {
  return (
    <SkillsStyled>
      <Title title={"My Skills"} span={"my skills"} />
      <InnerLayout>
        <div className="skills">
          <ProgressBar
            title={"HTML5"}
            width={"90%"}
            text={"90%"}
            icon={["fab", "html5"]}
            colores="#ce5001"
            link="https://developer.mozilla.org/es/docs/Web/HTML"
          />
          <ProgressBar
            title={"CSS3"}
            width={"90%"}
            text={"90%"}
            icon={["fab", "css3"]}
            colores="#006bb4"
            link="https://www.w3schools.com/css/css_intro.asp"
          />
          <ProgressBar
            title={"JavaScript"}
            width={"85%"}
            text={"85%"}
            icon={["fab", "js"]}
            colores="#efd81d"
            link="https://www.w3schools.com/js/"
          />
          <ProgressBar
            title={"Node.js"}
            width={"85%"}
            text={"85%"}
            icon={["fab", "js"]}
            colores="#efd81d"
            link="https://www.w3schools.com/nodejs/"
          />
          <ProgressBar
            title={"React JS"}
            width={"80%"}
            text={"80%"}
            icon={["fab", "node"]}
            colores="#00d1f7"
            link="https://www.w3schools.com/REACT/DEFAULT.ASP"
          />
          <ProgressBar
            title={"Angular"}
            width={"20%"}
            text={"20%"}
            icon={["fab", "angular"]}
            colores="#00d1f7"
            link="https://www.w3schools.com/angular/"
          />
          <ProgressBar
            title={"Illustrator"}
            width={"90%"}
            text={"90%"}
            icon={faCamera}
            colores="#00d1f7"
            link="https://www.adobe.com/la/products/illustrator/free-trial-download.html"
          />
          <ProgressBar
            title={"Photoshop"}
            width={"70%"}
            text={"70%"}
            icon={faCamera}
            colores="#00d1f7"
            link="https://www.adobe.com/la/products/photoshop.html"
          />
          <ProgressBar
            title={"Figma"}
            width={"60%"}
            text={"60%"}
            icon={faCamera}
            colores="#00d1f7"
            link="https://www.figma.com/"
          />
        </div>
      </InnerLayout>
    </SkillsStyled>
  );
}

const SkillsStyled = styled.section`
  margin-top: 50px;
  .skills {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 2rem;
    grid-column-gap: 3rem;
    @media screen and (max-width: 700px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
`;

export default Skills;
library.add(fab);
