/* eslint-disable react/jsx-no-target-blank */
import React from "react";
import styled from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleRight } from "@fortawesome/free-solid-svg-icons";
function ProgressBar({ title, width, text, icon, colores, link }) {
  return (
    <ProgressBarStyled>
      <div className="background">
        <a href={link} target="_blank">
          <p>Official site</p>
          <i>
            <FontAwesomeIcon icon={faAngleRight} className="icon-r" />
          </i>
        </a>
      </div>
      <div className="general">
        <h6>{title}</h6>
        <div className="progress-bar">
          <div style={{ color: colores }}>
            <FontAwesomeIcon icon={icon} />
          </div>
          <p>{text}</p>
          <div className="progress">
            <span style={{ width: width }}></span>
            <span className="w100"></span>
          </div>
        </div>
      </div>
    </ProgressBarStyled>
  );
}

const ProgressBarStyled = styled.div`
  background-color: var(--border-color);
  padding: 10px 20px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  .general {
    transition: opacity 1s ease;
  }
  .background {
    position: absolute;
    display: flex;
    align-items: center;
    width: 100%;
    top: 0;
    right: 100vw;
    height: 100%;
    background: #000;
    opacity: 0.9;
    z-index: 3;
    color: #fff;
    transition: right 0.7s ease;
    a {
      display: flex;
      width: 100%;
      p {
        flex: 7;
        padding-left: 30px;
        font-size: 25px;
        font-family: "Bungee Inline";
      }
      i {
        flex: 3;
        width: 100%;
        text-align: center;
        .icon-r {
          animation-name: next;
          animation-duration: 1s;
          animation-iteration-count: infinite;
        }
      }
      @keyframes next {
        50% {
          transform: scale(1.4);
          color: var(--primary-color);
        }
        100% {
          transform: scale(1);
        }
      }
    }
  }
  &:hover {
    .general {
      opacity: 0;
    }
    .background {
      right: 0;
    }
  }
  .progress-bar {
    display: flex;
    align-items: center;
    p {
      padding-right: 1.1rem;
      padding-left: 1rem;
      color: var(--white-color);
    }
    .progress {
      position: relative;
      width: 100%;
      height: 0.4rem;
      span {
        position: absolute;
        left: 0;
        bottom: 0;
        height: 100%;
        background-color: var(--primary-color);
        z-index: 2;
      }
      .w100 {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background-color: var(--white-color);
        z-index: 1;
      }
    }
  }
`;

export default ProgressBar;
